import React, { createContext, Fragment } from 'react';
import { BackBtn } from '../../Home'

export const UserContext = createContext();

const KeyTxt = (props) => {
    const product = {
        name: '',
        brand: '',
        model: '13',
        year: '2022'
    };

    return (
        <Fragment>
            <UserContext.Provider value={product}>{props.children}</UserContext.Provider>
            <BackBtn />
        </Fragment>
    );
};

export default KeyTxt;