import { Fragment, useState } from "react";
import './tab.module.css';
import { BackBtn } from '../../Home'


export default function TabMenu() {
  const [activeTab, setActiveTab] = useState(0);

  const tabClickHandler = (index) => {
    setActiveTab(index);
    // tabClickHandler  () index  setActiveTab useState()  Props 
  };

  const tabContArr = [
    {
      //     
      // 1. is-active  
      //  setActiveTab () useState  
      tabTitle: (
        <li className={`{activeTab === 0 ? "is-active" : "" } tabName`}
          onClick={() => tabClickHandler(0)}
        >  </li>
      ),
      tabCont: (
        <div className={`{activeTxt === 0 ? "is-active" : "" }`}>
          <p>
             React    .<br></br>
                . HTML   div    .<br></br>
                React DOM    (root) DOM  .
            React      DOM  .<br></br> React           DOM    .
            React      DOM  ReactDOM.createRoot()  , React  root.render()  .
          </p>
          <span>(Rendering)</span>
          <p>
                 <br />
            * State     (trigger)
            .<br />
            * (Rendering) :          .<br />
             ,    ==   == <br />
             ?       .<br />
            ex) ,   , useState() set  
          </p>
        </div>
      )
    },
    {
      tabTitle: (
        <li className={`{activeTab === 1 ? "is-active" : "" } tabName`}
          onClick={() => tabClickHandler(1)}
        > JSX</li>
      ),
      tabCont: (
        <div className={`{activeTxt === 1 ? "is-active" : "" }`}>
          <p>
            JSX  JavaScript  . UI     
            React    . JSX      ,
            JavaScript    .<br></br><br></br>
            JSX?<br></br>
            React    UI (  ,   state  ,       )   .
            React         ,          .     , JS          .<br></br><br></br>
            React JSX   ,   JavaScript   UI         .  React           .
              !
          </p>
        </div>
      )
    }
  ];

  return (
    <Fragment>
      <div className="tabWrap">
        <p>TAP  test</p>
        <ul className="tabs">
          {tabContArr.map((section, index) => {
            return section.tabTitle
          })}
        </ul>
        <div className="tabContents">
          {tabContArr[activeTab].tabCont}
        </div>
      </div>
      <BackBtn />
    </Fragment>
  )
}