import styled, { ThemeProvider } from 'styled-components';
import { useState, props } from 'react';
import theme from './component/styles/Tagged';
import {
  BrowserRouter,
  Routes,
  Route,
  Link,
  useNavigate,
} from 'react-router-dom';
import SectionBegin from './component/section/section';
import './App.css';
import { Fade, Slide } from 'react-reveal';

export default function Home(props) {
  const Body = styled.body`
    height: 100vh;
    ${({ theme }) => theme.commonStyle.homeStyle};
  `;

  const HomeH2 = styled.h2`
    font-size: ${({ theme }) => theme.fontSizeObj.title};
    margin-bottom: 96px;
  `;

  const Container = styled.ul`
    width: 100vw;
    display: block;
    height: auto;
    overflow: hidden;
    ${({ theme }) => theme.commonStyle.homeStyle};
  `;
  return (
    <ThemeProvider theme={theme}>
    <nav className="homeNav">
      <HomeH2> Study Project</HomeH2>
      <Container>
      <li>
          <Link to={'/profile'}>  </Link>
        </li>
        <li>
          <Link to={'/weather'}> OPEN API   </Link>
        </li>
        <li>
          <Link to={'/jsx'}>JSX - setState()   Test</Link>
        </li>
        <li>
          <Link to={'/props'}>props() Function Class  </Link>
        </li>
        <li>
          <Link to={'/usestate'}>useState()  </Link>
        </li>
        <li>
          <Link to={'/useref'}>useref() </Link>
        </li>
        <li>
          <Link to={'/useeffect'}> useEffect()  </Link>
        </li>
        <li>
          <Link to={'/cleanup'}> useEffect() </Link>
        </li>
        <li>
          <Link to={'/testcontext'}>useContext()  style  </Link>
        </li>
        <li>
          <Link to={'/impcontext'}>useContext() </Link>
        </li>
        <li>
          <Link to={'/slider'}>React Slider  Test</Link>
        </li>
        <li>
          <Link to={'/styledobj'}> Styled-components </Link>
        </li>
        <li>
          <Link to={'/api'}> Axios  </Link>
        </li>
        <li>
          <Link to={'/dummylist'}> dummy data  </Link>
        </li>
      </Container>
    </nav>
    </ThemeProvider>
  );
}

export function BackBtn() {
  const navigate = useNavigate();
  const goBack = () => {
    navigate(-1);
  };
  return (
    <div>
      <button type="button" id="backPage" onClick={goBack}>
        
      </button>
    </div>
  );
}
