import { React } from 'react';
import styled, { ThemeProvider } from 'styled-components';
// style-components  ThemeProvider  import      
import { BiAlignRight } from 'react-icons/bi';
import theme from '../styles/Tagged';
import { BackBtn } from '../../Home';
//      theme  import

const Container = styled.div`
  width: 100vw;
  height: 100vh;
  margin: auto;
  ${({ theme }) => theme.commonStyle.flexCenterColumn};
  ${({ theme }) => theme.divTxtStyle.textAlignR};
`;
//     : theme        props?
//         string  
// ${({ obj }) => props }

const Title = styled.h2`
  font-size: ${({ theme }) => theme.fontSizeObj.title};
  color: ${({ theme }) => theme.colors.black};
`;

const Subtitle = styled.h3`
  font-size: ${({ theme }) => theme.fontSizeObj.subtitle};
  color: ${({ theme }) => theme.colors.grey};
`;

const Paragraph = styled.p`
  font-size: ${({ theme }) => theme.fontSizeObj.paragraph};
  color: ${({ theme }) => theme.colors.white};
`;

const Box = styled.section`
  background: ${(props) => props.color || 'grey'};
  padding: 1rem;
  display: flex;
  ${
    '' /*    1024px          
  768px    . */
  }
  width: 1024px;
  margin: 0 auto;
  @media (max-width: 1024px) {
    width: 768px;
  }
  @media (max-width: 768px) {
    width: 100%;
  }
`;

const MenuBar = styled.a`
  display: flex;
  width: 48px;
  align-items: center;
  font-size: 30px;
  cursor: pointer;
  ${
    '' /* position: absolute;
  right: 32px;
  height: 96px; */
  }
  @media screen and (min-width: 768px) {
    display: none;
  }
`;

const StyledObj = () => {
  return (
    <ThemeProvider theme={theme}>
      <Box>
        <Container>
          <Title>Styled-Components Provider </Title>
          <MenuBar>
            <BiAlignRight />
          </MenuBar>
          <Subtitle>
            ThemeProvider     
          </Subtitle>
          <Paragraph>
              ,     
                , flex   
                
          </Paragraph>
        </Container>
      </Box>
      <BackBtn />
    </ThemeProvider>
  );
};
export default StyledObj;
