import styled, { css } from 'styled-components';
import BackBtn from '../../Home';

const Box = styled.div`
   {
    /* props        . */
  }
  background: ${(props) => props.color || 'blue'};
  padding: 1rem;
  display: flex;
`;

const Button = styled.button`
    background: white;
    color : black;
    border-radius: 4px;
    padding: 0.5rem;
    display: flex;
    align-items : center;
    justify-content: center;
    box-sizing: border-box;
    font-size: 1rem;
    font-weight: 600;

    { /*   Sass      */}
    &:hover {
        background: rgba(255, 255, 255, 0.9);
    }

    /*   inverted  true    . */}
    ${(props) =>
      props.inverted &&
      css`
        background: none;
        border: 2px solid white;
        color: white;
        &:hover {
          backrground: white;
          color: black;
        }
      `};
        & + button {
            margin-left: 1rem;
    }
`;

const StyledComponent = () => (
  <>
    <StyleWrap>
      <h1>CssModule</h1>
      <Box color="black">
        <Button></Button>
        <Button inverted={true}></Button>
        {/* inverted attribute    css */}
      </Box>
    </StyleWrap>
    <BackBtn />
  </>
);

const StyleWrap = styled.div`
  max-width: 768px;
  width: 100%;
  margin: 0 auto;
`;

export default StyledComponent;
